  margin: 0;
  font-family: "Calibri";
  color: #164659;
  font-weight: 700;
  font-size: 20px;
  list-style: none;
  margin-right: 52rem;
  margin-left: 40rem;
  
  
}
.hambug{
  display: none;
}
.header-logo{
  margin-left: 12rem;
  width: 3.8rem;
}















